






































import { Vue, Component, Prop } from 'vue-property-decorator';
import { VApp, VContent, VContainer, VRow, VCol, VCard, VSheet, VCardText, VCardTitle, VTimeline, VTimelineItem } from 'vuetify/lib';
import vuetify from '../plugins/vuetify';

interface Report {
  createdAt: Date;
  updatedAt: Date;
  id: number;
  content: string;
}

@Component({
  vuetify,
  components: {
    VApp, VContent, VContainer, VRow, VCol, VSheet, VCard, VCardText, VCardTitle, VTimeline, VTimelineItem,
  },
})
export default class FishingReports extends Vue {
  @Prop({ default: 'UWJFK6FVUA29Q' }) private merchantid!: string;

  private reports: Report[] = [];
  private readonly BACKEND = process.env.VUE_APP_BACKEND_HOST;

  public async mounted() {
    const resp = await fetch(this.BACKEND + `/info/${this.merchantid}/reports`);
    this.reports = await resp.json();
  }
}
